export default {
	data() {
		return {
			newslist: []
		};
	},
	onLoad() {
		uni.showLoading({
			title: '新闻',
			mask: false
		});
		uni.request({
			url: '../../static/data/news.json',
			method: 'GET',
			data: {},
			success: res => {
				this.newslist = res.data
				uni.hideLoading()
			},
			fail: (err) => {console.log(err)},
			complete: () => {}
		});
	},
	methods: {
		todetail(e) {
			let newsid = e.currentTarget.dataset.newsid
			uni.navigateTo({
				url: '../detail/detail?newsId=' + newsid
			})
		}
	}
}
